import React, { Component } from 'react'
import {BrowserRouter as Router,Route} from 'react-router-dom';
import {NavLink} from 'react-router-dom';
import All from './home/All'
import Essence from './home/Essence'
import Share from './home/Share'
import Question from './home/Question'
import Recruitment from './home/Recruitment'
import Test from './home/Test'

export default class Home extends Component {
    render() {
        let {url} = this.props.match
        return (
            <div>
                <div className="header">
                    <NavLink to={`${url}/all`} className="topic-tab" activeClassName="active-tab" activeStyle={{color:'#fff'}}>全部</NavLink>
                    <NavLink to='/home/essence' className="topic-tab" activeClassName="active-tab" activeStyle={{color:'#fff'}}>精华</NavLink>
                    <NavLink to='/home/share' className="topic-tab" activeClassName="active-tab" activeStyle={{color:'#fff'}}>分享</NavLink>
                    <NavLink to='/home/question' className="topic-tab" activeClassName="active-tab" activeStyle={{color:'#fff'}}>问答</NavLink>
                    <NavLink to='/home/recruitment' className="topic-tab" activeClassName="active-tab" activeStyle={{color:'#fff'}}>招聘</NavLink>
                    <NavLink to='/home/test' className="topic-tab" activeClassName="active-tab" activeStyle={{color:'#fff'}}>客户端测试</NavLink>
                </div>
                <div className="inner no-padding">
                    <Route path='/' component={All} exact/>
                    <Route path='/home' component={All} exact/>
                    <Route path='/home/all/:id' component={All} />
                    <Route path='/home/all' component={All} exact/>
                    <Route path='/home/essence' component={Essence} exact/>
                    <Route path='/home/essence/:id' component={Essence} />
                    <Route path='/home/share' component={Share} exact/>
                    <Route path='/home/share/:id' component={Share} />
                    <Route path='/home/question' component={Question} exact/>
                    <Route path='/home/question/:id' component={Question} />
                    <Route path='/home/recruitment' component={Recruitment} exact/>
                    <Route path='/home/recruitment/:id' component={Recruitment} />
                    <Route path='/home/test' component={Test} />
                </div>
            </div>
        )
    }
}
