import React, { Component } from 'react'

export default class Login extends Component {
    render() {
        return (
            <div>
                <div className="header" style={{background:'#f6f6f6'}}>
                    <ul className="breadcrumb">
                        <li><a href="/">主页</a><span className="divider">/</span></li>
                        <li className="active">登录</li>
                    </ul>
                </div>
                <div className="inner">
                    <form id="signin_form" className="form-horizontal" action="#" method="post">
                        <div className="control-group">
                            <label className="control-label" for="name">用户名</label>
                            <div className="controls">
                                <input className="input-xlarge" id="name" name="name" size="30" type="text" />
                            </div>
                        </div>
                        <div className="control-group">
                            <label className="control-label" for="pass">密码</label>
                            <div className="controls">
                                <input className="input-xlarge" id="pass" name="pass" size="30" type="password" />
                            </div>
                        </div>
                        <div className="form-actions" style={{paddingLeft:'180px'}} >
                            <input type="submit" className="span-primary" value="登录" />
                            <a href="/auth/github">
                                <span className="span-info">
                                    通过 GitHub 登录
                                </span>
                            </a>
                            <a id="forgot_password" href="/search_pass">忘记密码了?</a>
                        </div>
                    </form>
                 </div>
            </div>
        )
    }
}
